.Button {
  padding: 15px;
  font-size: 12px;
  text-transform: uppercase;
  border: 1px solid rgb(170, 170, 170);
}

.Button:hover {
  background: #24c5fc;
  border: 1px solid #24c5fc;
  color: #ffffff;
  outline: none;
}

.Button:focus,
button.active {
  background: #2196f3;
  border: 1px solid #2196f3;
  color: #ffffff;
  outline: none;
}
